import { Component, OnInit, Input } from '@angular/core';
import { FormGroup, FormBuilder, FormControl, Validators} from '@angular/forms';
import { MainServicesService } from '../../../services/main-services.service';
import { Router } from '@angular/router';
import { ActivatedRoute } from '@angular/router';
import { ModalController } from "@ionic/angular";
import { NavParams } from '@ionic/angular';
import $ from 'jquery';



@Component({
  selector: 'app-edit-user-modal',
  templateUrl: './edit-user-modal.component.html',
  styleUrls: ['./edit-user-modal.component.scss'],
})
export class EditUserModalComponent implements OnInit {
  editUserForm:FormGroup;
  branchesList;
  departmentsList;
  departments;
  userData:{name?,gender?,email?,phone?,is_whatsapp_enabled?,branch?,department?,role?};
  rolesList;
  @Input() _id: any;
  getId = this.navParams.get('_id');
  valueUserName = true;
  valueUserPhone = true;
  valueUserEmail = true;
  valueUserPsw = true;
  valuebranch = true;
  line_value_name="none";
  line_value_mobile="none";
  line_value_gender="none";
  line_value_email="none";
  line_value_password="none";
  line_value_branch = "none";
  line_value_role="none";
  showBtn;
  line_value_dept = "none";
  checkEmail;
  checkPhone;
  checkBranch;
  checkDept;
  checkRole;
  errName;
  errPhone;
  errMail;
  filesToUpload: any[] = [];
  selectedFile:any = [];
  imageData;
  name;
  gender;
  phone;
  email;
  branch;
  department;
  role;
  isRole;
  showFields:boolean = true;
  extension;
  displayImage;
  showExtError;
  extensionErr;
  disablebranch= true;
  disabledbtn=true;
  disablegender=true;
  disablerole=true;
  checkWatsapp:boolean;
  getPermission;
  userId;
  roleRight:boolean=false;
  deptRight:boolean=false;
  branchRight:boolean=false;
  emailRight:boolean=false;
  phoneRight:boolean=false;
  genderRight:boolean=false;
  nameRight:boolean=false;
  getValue;
 isLoading=false;



  constructor(private fb : FormBuilder, 
    private route: ActivatedRoute, 
    private router : Router,
    private main_serives :MainServicesService,private navParams:NavParams,
    private modalController: ModalController,private notificationService: MainServicesService) {
      
  	this.editUserForm = this.fb.group({
  		name: ['',[Validators.required,Validators.pattern("[a-zA-Z ]+")]],
  		gender : ['',[Validators.required]],
  		phone:['',[Validators.required,Validators.pattern("^((\\+91-?)|0)?[0-9]{10}$")]],
      is_whatsapp_enabled:[false],
  		email:['',[Validators.required,Validators.email]],
      // password:['',Validators.required],
  		branch : ['',[Validators.required]],
  		department : ['',[Validators.required]],
  		role : ['',[Validators.required]]
  	})   
    
    this.getPermission = JSON.parse(localStorage.getItem('login_permission'));
    this.userId = this.getPermission.id;
  }

  ngOnInit() {
   
   $('document').ready(function(){
     $('#extError').click(function(){
       alert('hello');
     })
   })
  
  }

  changeToggle(){
    this.showBtn = true;
  }


  tokenImage(event){
    this.showBtn = true;
    this.selectedFile = event.target.files[0];

    this.filesToUpload.push(this.selectedFile);

    var fileName = this.selectedFile.name;

    this.extension = fileName.split('.').pop();

    var reader = new FileReader();

    if(this.extension == 'png' || this.extension == 'jpg' || this.extension == 'jpeg' || this.extension == 'gif' || this.extension == 'webp')
    {
      reader.onload = (e:any) =>{
        $('#blah').attr('src', e.target.result);
      } 
      reader.readAsDataURL(this.selectedFile); // convert to base64 string
      this.extensionErr = false;

    }
    else{
    
      this.showExtError = "Couldn't set profile photo.";
      this.extensionErr = true;

    
      // $(document).ready(function(){
        
      // $('#extError').fadeIn(1000);
      // $('#extError').fadeOut(5000);
   
      // })
    }
    
  }

  editUserName(){
    this.showBtn = true;
    $('.name').addClass('selection-border');
    
    $('.branch').removeClass('selection-border');
    $('.email').removeClass('selection-border');
    $('.gender').removeClass('selection-border');
    $('.phone').removeClass('selection-border');
    $('.dept').removeClass('selection-border');    
    $('.role').removeClass('selection-border');
    // this.line_value_name = "line";
    this.valueUserName = false;

    this.disablebranch=true;
    this.valueUserEmail = true;
    this.disablegender=true;
    this.valueUserPhone = true;
    this.disabledbtn=true;
    this.disablerole=true; 
   
  }
  InsertData(){
    $('.name-pencil').hide();
    this.nameRight=true;
  }

  editBranch(){
    $('.branch').addClass('selection-border');

    $('.name').removeClass('selection-border');
    $('.email').removeClass('selection-border');
    $('.gender').removeClass('selection-border');
    $('.phone').removeClass('selection-border');
    $('.dept').removeClass('selection-border');    
    $('.role').removeClass('selection-border');
     this.showBtn = true;
    // this.line_value_branch = "line";
    // $('#selectbranch').attr('disabled',false);
    // $('.branch-pencil').hide(); 
    this.valueUserName = true;    
    this.valueUserEmail = true;
    this.disablegender=true;
    this.valueUserPhone = true;
    this.disabledbtn=true;
    this.disablerole=true; 

    this.disablebranch=false;
  }

  applyBranch(){
    $('.branch-pencil').hide();
    this.branchRight=true;
  }
 

  editUserEmail(){
    this.showBtn = true;

    $('.email').addClass('selection-border');

    $('.name').removeClass('selection-border');
    $('.branch').removeClass('selection-border');
    $('.gender').removeClass('selection-border');
    $('.phone').removeClass('selection-border');
    $('.dept').removeClass('selection-border');    
    $('.role').removeClass('selection-border');
    // this.line_value_email = "line";

    this.valueUserName = true;    
     this.disablebranch=true;
    this.disablegender=true;
    this.valueUserPhone = true;
    this.disabledbtn=true;
    this.disablerole=true; 



    this.valueUserEmail = false;


  }
  InsertEmail(){
    $('.email-pencil').hide();
    this.emailRight=true;
  }
  editGender(){
    $('.gender').addClass('selection-border');

    $('.name').removeClass('selection-border');
    $('.branch').removeClass('selection-border');
    $('.email').removeClass('selection-border');
    $('.phone').removeClass('selection-border');
    $('.dept').removeClass('selection-border');    
    $('.role').removeClass('selection-border');
    this.showBtn = true;
    // this.line_value_gender = "line";

    this.valueUserName = true;    
    this.disablebranch=true;
    this.valueUserEmail=true;
    this.valueUserPhone = true;
    this.disabledbtn=true;
    this.disablerole=true; 


    this.disablegender=false;
  }
  applyGender(){
    $('.gender-pencil').hide();
    this.genderRight=true;
  }
  editUserPhone(){
    this.showBtn = true;
    $('.phone').addClass('selection-border');

    $('.name').removeClass('selection-border');
    $('.branch').removeClass('selection-border');
    $('.email').removeClass('selection-border');
    $('.gender').removeClass('selection-border');
    $('.dept').removeClass('selection-border');    
    $('.role').removeClass('selection-border');
    // this.line_value_mobile = "line";
    this.valueUserPhone = false;


    this.valueUserName = true;    
    this.disablebranch=true;
    this.valueUserEmail=true;
    this.disablegender = true;
    this.disabledbtn=true;
    this.disablerole=true; 
  }
  InsertPhone(){
    $('.number-pencil').hide();
    this.phoneRight=true;
  }
  editUserPsw(){
    this.showBtn = true;
    // this.line_value_password = "line";
    this.valueUserPsw = false;
  }
  editDepartment(){
    $('.dept').addClass('selection-border');

    $('.name').removeClass('selection-border');
    $('.branch').removeClass('selection-border');
    $('.email').removeClass('selection-border');
    $('.gender').removeClass('selection-border');
    $('.phone').removeClass('selection-border');    
    $('.role').removeClass('selection-border');
    this.showBtn = true;
    // this.line_value_dept = "line"; 
    this.disabledbtn=false;

    this.valueUserName = true;    
    this.disablebranch=true;
    this.valueUserEmail=true;
    this.disablegender = true;
    this.valueUserPhone=true;
    this.disablerole=true; 
  }
  applyDept(){
    $('.department-pencil').hide();
    this.deptRight=true;
  }
  editRole(){
    $('.role').addClass('selection-border');

    $('.name').removeClass('selection-border');
    $('.branch').removeClass('selection-border');
    $('.email').removeClass('selection-border');
    $('.gender').removeClass('selection-border');
    $('.phone').removeClass('selection-border');    
    $('.dept').removeClass('selection-border');
    this.showBtn = true;
    // this.line_value_role = "line";
    this.disablerole=false; 


    this.valueUserName = true;    
    this.disablebranch=true;
    this.valueUserEmail=true;
    this.disablegender = true;
    this.valueUserPhone=true;
    this.disabledbtn=true; 
  }
  applyrole(){
    $('.role-pencil').hide();
    this.roleRight=true;
  }
  

  get_branches(){
    this.isLoading=true;
    this.main_serives.getBranchesList().subscribe((result)=>{
      this.isLoading=false;
      this.branchesList=result['branches'];
   });
 }
 get_depts(){
  this.isLoading=true;
   this.main_serives.getDepartmentsList().subscribe((result)=>{
    this.isLoading=false;
      this.departmentsList=result['departments'];
      this.departments=result['departments'];
   });
 }
 get_roles(){
  this.isLoading=true;
    this.main_serives.getRolesList().subscribe((result)=>{
      this.isLoading=false;
      this.rolesList=result['roles'];
   });
 }

 branch_name(e){
  this.departmentsList = this.departments.filter((department)=>{
    return department.branch._id == e.target.value
  });
  this.editUserForm.get('department').setValue('');
  
  // this.branch = e.target.value
}

 ionViewWillEnter() {

  $(document).ready(function(){
    $('#mat-select-value-1').addClass('roleSelect');
  });
  
  this.get_branches();
  this.get_depts();
  this.get_roles();
  this.isLoading=true;
  this.main_serives.getUserDetails(this.getId).subscribe(
    (data)=>{
      this.isLoading=false;
      this.userData = data['user'];
      this.checkWatsapp = data['user']['is_whatsapp_enabled'];
      this.imageData = data['user']['image']?data['user']['image'].url:'';
      this.isRole = data['user']['role']['name'];
      
      if(!this.userData.branch  || this.userData.department){
        this.showFields = false;
      }
      this.editUserForm = this.fb.group({
        name: [this.userData.name],
        gender : [this.userData.gender],
        phone:[this.userData.phone],
        is_whatsapp_enabled:[this.userData.is_whatsapp_enabled],
        email:[this.userData.email],
        branch : [this.userData.branch?this.userData.branch._id:''],
        department : [this.userData.department?this.userData.department._id:''],
        role : [this.userData.role._id]
      })
      
    },
    (error) => {
      console.log(error);
    }
  ) 
}

editUser(value){
  this.router.navigateByUrl['/home'];
const formData = new FormData();


formData.append('name', value.name);
formData.append('phone',value.phone);
formData.append('is_whatsapp_enabled',value.is_whatsapp_enabled);
formData.append('email',value.email);
formData.append('gender',value.gender); 
formData.append('branch',value.branch);
formData.append('department',value.department);
formData.append('role',value.role);

  if(this.selectedFile.length == 0 ){
    formData.append('image',this.imageData);
  }
  else{
    if(this.extension == 'png' || this.extension == 'jpg' || this.extension == 'jpeg' || this.extension == 'gif' || this.extension == 'webp'){
    formData.append('image',this.selectedFile);
  }
}
  


// return 0;
  this.main_serives.editUserDetails(this.getId,formData).subscribe(
    (result)=>{
      this.modalController.dismiss(result,'success');
      this.main_serives.filter('click');

    },
    (error)=>{
      console.log(error);
      this.errName = error['error']['error']['name'];
      this.errPhone = error['error']['error']['phone'];
      this.errMail = error['error']['error']['email']
      if(this.errName || this.errPhone || this.errMail){
        $('#errors').fadeIn(1000);
      }
    }
  )

}

dismiss() {
  this.modalController.dismiss({
    'dismissed': true
  });

 
}

}
