import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { MainServicesService } from './../../../services/main-services.service';
import { AlertController, ModalController } from "@ionic/angular";
import { AddUserModalComponent } from '../add-user-modal/add-user-modal.component';
import { EditUserModalComponent } from '../edit-user-modal/edit-user-modal.component';
import $ from 'jquery';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-users-list',
  templateUrl: './users-list.page.html',
  styleUrls: ['./users-list.page.scss'],
})
export class UsersListPage implements OnInit {
  setupData= new Array();
  searchText;

  sucmsg
  errmsg;
  pageCounts=Array();  // Will Push total no. of pages in this array
  pageCount:number;    // Total no. of Pages for Users List (data from API)
  page:number;         // Current Page (data from API)
  leftSize: number;
  rightSize: number;
  showMenu: boolean = false;
  isLoading=false;

  constructor(private main_services: MainServicesService, private router: Router, private modalController: ModalController,
     private alertCtrl: AlertController, private toastr: ToastrService) { }

  ngOnInit() {
  }

  manuToggle() {
    this.showMenu = !this.showMenu;
    if (this.showMenu == false) {
      if ($(window).width() < 992) {
        this.rightSize = 12;
      }
      else if ($(window).width() < 1200) {
        this.leftSize = 3;
        this.rightSize = 9;
      }
      else {
        this.leftSize = 2;
        this.rightSize = 10;
      }
      $('#firstdiv').removeClass("small-aside");
      $('#seconddiv').removeClass("big-right-side");
    }
    else {
      if ($(window).width() < 992) {
        this.rightSize = 12;
      }
      else if ($(window).width() < 1200) {
        this.leftSize = 1;
        this.rightSize = 11;
      }
      else {
        this.leftSize = 1;
        this.rightSize = 11;
      }
      $('#firstdiv').addClass("small-aside");
      $('#seconddiv').addClass("big-right-side");
    }
  }

  hide_mobile_menu() {
    $('#close-show-991').click(function () {
      $('#firstdiv').removeClass("small-aside");
    })
  }

  showMenuMob = true;
  mobileManuToggle() {
    $('#firstdiv').addClass("small-aside");
    // this.showMenuMob = !this.showMenuMob;
  }


  ionViewWillEnter() {
    if ($(window).width() < 992) {
      this.rightSize = 12;
    }
    else if ($(window).width() < 1367) {
      this.leftSize = 3;
      this.rightSize = 9;
    }
    else {
      this.leftSize = 2;
      this.rightSize = 10;
    }
    // this.loader.getdata(true)
    this.pageCounts= [];
    this.setupData = [];
    this.isLoading=true;
    this.main_services.getUsersList().subscribe(
      (result) => {
        this.isLoading=false;
        this.pageCount  = result['pageCount'];
        this.page       = result['page'];
        for(let i=1; i<=result['pageCount']; i++)
        {
          this.pageCounts.push(i);
        }      
        result['users'].forEach((data) => {
          this.setupData.push({
            branch              : data['branch']? data['branch']['name'] : "-",
            department          : data['department']? data['department']['name'] : "-",
            email               : data['email'],
            gender              : data['gender'],
            is_whatsapp_enabled : data['is_whatsapp_enabled'],
            name                : data['name'],
            phone               : data['phone'],
            role                : data['role']['name'],
            _id                 : data['_id'],
            tokenCount          : data['token_data']['count'],
            image               : data['image']?data['image'].url:''
          });
        });
    })
  }

  paginationData(pageNo){
    this.setupData = [];
    this.isLoading=true;
    this.main_services.userPagination(pageNo).subscribe(
      (result) => {
        this.isLoading=false;
        this.page       = result['page'];      
        result['users'].forEach((data) => {
          this.setupData.push({
            branch              : data['branch']? data['branch']['name'] : "-",
            department          : data['department']? data['department']['name'] : "-",
            email               : data['email'],
            gender              : data['gender'],
            is_whatsapp_enabled : data['is_whatsapp_enabled'],
            name                : data['name'],
            phone               : data['phone'],
            role                : data['role']['name'],
            _id                 : data['_id'],
            tokenCount          : data['token_data']['count']
          });
        });
    })
  }
  async openModal() {
    const modal = await this.modalController.create({
      component: AddUserModalComponent,
      cssClass: 'my-user-custom-class'
    });
    await modal.present();

    const { data: result, role } = await modal.onWillDismiss();
    this.sucmsg = result['message'];


    if (result['success'] == true) {
      this.toastr.success(this.sucmsg);
      this.ionViewWillEnter();
    }
  }


  
async openEditModal(user) {
  const modal = await this.modalController.create({
    component: EditUserModalComponent,
    componentProps: {
      _id: user._id,
    },
    cssClass: 'my-user-edit-custom-class'
  });


  await modal.present();

  const { data: result, role } = await modal.onWillDismiss();
  this.sucmsg = result['message'];

  if (result['success'] == true) {
    this.toastr.success(this.sucmsg);
    this.ionViewWillEnter();
  }


}

async presentAlert(id) {
  const alert = await this.alertCtrl.create({
    cssClass: 'my-alert-class',
    subHeader: 'Do you really want to delete it ?',
    buttons: [{
      text: 'Cancel'
    }, {
      text: 'yes',
      handler: () => {
        this.delete_user(id)
      }
    }]
  });

  await alert.present();
}


  delete_user(id) {
    this.isLoading=true;
    this.main_services.deleteUserDetails(id).subscribe(
      (data) => {
        this.isLoading=false;
        this.sucmsg = data['message'];
        this.toastr.success(this.sucmsg);
        this.ionViewWillEnter();
      }, (error) => {
        console.log(error);
      }
    )
  }
   

  user_search(searchData) {
    this.isLoading=true;
    this.main_services.searchuserApi(searchData).subscribe(

      (data) => {
        this.isLoading=false;
        this.setupData=[];
        data['users'].forEach((result) => {
          this.setupData.push({
            name                : result['name'],
            phone               : result['phone'],
            role                : result['role']['name'],
            image               : result['image']?result['image'].url:'',
            tokenCount          : result['token_data']['count'],
            _id                 : result['_id']
          });
        });
      }

    )


  }

















}
