import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import $ from 'jquery';
import { MainServicesService } from './../../../services/main-services.service';
import { ModalController,NavController } from "@ionic/angular";
import { LoaderService } from 'src/app/services/loader.service';

@Component({
  selector: 'app-add-user-modal',
  templateUrl: './add-user-modal.component.html',
  styleUrls: ['./add-user-modal.component.scss'],
})
export class AddUserModalComponent implements OnInit {
  branchesList:any;
  departmentsList:any;
  departments:any;
  rolesList:any;
  userName:string="";
  phone:string="";
  is_watsapp;
  email:string="";
  gender:string="";
  branch:string="";
  dept:string="";
  role:string="";
  psw:string="";
  errMsg;
  phoneErr;
  emailErr;
  brancherr;
  departmenterr;
  rollerr;
  emailerr;
  gendererr;
  passworderr;
  public pepperoni: any = false;
  filesToUpload: any[] = [];
  images = new Array();
  selectedFile:any = [];
 
 
  constructor(private router: Router, private main_services: MainServicesService,
     private modalController: ModalController,public navCtrl: NavController, private loader:LoaderService) { }
  
  ngOnInit() {}
  get_branches(){
    this.main_services.getBranchesList().subscribe((result)=>{
      this.branchesList=result['branches'];
   });
 }
 get_depts(){
  this.main_services.getDepartmentsList().subscribe((result)=>{
    this.departmentsList=result['departments'];
    this.departments=result['departments'];
  })   
  
 }
 get_roles(){
    this.main_services.getRolesList().subscribe((result)=>{
      this.rolesList=result['roles'];
   });
 }
 
 ionViewWillEnter() {

  this.get_branches();
  this.get_depts();
  this.get_roles();

}
user_name(e){
  this.userName = e.target.value
}
user_phone(e){
  this.phone = e.target.value
}
changeToggle(){
  this.is_watsapp = this.pepperoni;
}
// updateDriverOnlineStatus(e) {
//   console.log(e.target.value);
  
//   // this.isOnline = !this.isOnline;
// }
user_email(e){
  this.email = e.target.value
}
user_psw(e){
  this.psw = e.target.value
}
user_gender(m ,f){
  if(m){
    this.gender=m.target.value
  }
  else{
   this.gender=f.target.value
  }

}
branch_name(e){

  this.branch=e.value
  $('.select-item-list-popup-models label.branch-label').hide();

  this.departmentsList = this.departments.filter((department)=>{
    return department.branch._id == e.value
  });
  
  this.branch = e.value

}
dept_name(e){
  this.dept=e.value
  $('.select-item-list-popup-models label.dept-label').hide();

}
roll_name(e){
  this.role=e.value
  $('.select-item-list-popup-models label.role-label').hide();
}

userProfileImg:any;
userProfileImgFile:any;

tokenImage(event) {  

  this.selectedFile = event.target.files[0];
  this.filesToUpload.push(this.selectedFile);

  var fileName = this.selectedFile.name;

  var extension = fileName.split('.').pop();

  var reader = new FileReader();

  if(extension == 'jpg' || extension == 'png' || extension == 'jpeg' || extension == 'gif' || extension == 'webp')
  {
    reader.onload = (e:any) =>{
      $('#blah').attr('src', e.target.result);
    
    }
  }

  reader.readAsDataURL(this.selectedFile); // convert to base64 string

  
  
  }
submit()
{
 let addUser;

 addUser = {
  name : this.userName,
  phone:this.phone,
  is_whatsapp_enabled:this.pepperoni,
  email:this.email,
  password:this.psw,
  gender:this.gender,
  branch:this.branch,
  department:this.dept,
  role:this.role ,
  image:this.selectedFile,
}

const formData = new FormData();

formData.append('name', addUser.name);
formData.append('phone',addUser.phone);
// formData.append('is_whatsapp_enabled', addUser.is_whatsapp_enabled);
formData.append('is_whatsapp_enabled', addUser.is_whatsapp_enabled);
formData.append('email',addUser.email);
formData.append('password',addUser.password);
formData.append('gender',addUser.gender); 
formData.append('branch',addUser.branch);
formData.append('department',addUser.department);
formData.append('role',addUser.role);
formData.append('image',addUser.image);

// return formData;



  this.main_services.addUserDetails(formData).subscribe((result)=>{
    this.modalController.dismiss(result, 'success');

  },
  (error)=>{
    console.log(error);
    this.errMsg = error['error']['error']['name'];
    this.emailErr = error['error']['error']['email'];
    this.passworderr = error['error']['error']['password'];
    this.phoneErr = error['error']['error']['phone'];
    this.departmenterr = error['error']['error']['department'];
    this.rollerr = error['error']['error']['role'];
    this.brancherr = error['error']['error']['branch'];
    this.gendererr = error['error']['error']['gender'];

    
    if(this.errMsg){
      $('#errors').fadeIn(1000);
    }
    if(this.brancherr){
      $('#errors').fadeIn(1000);
    }
    if(this.emailErr){
      $('#errors').fadeIn(1000);
    }
    if(this.passworderr){
      $('#errors').fadeIn(1000);
    }
    if(this.phoneErr){
      $('#errors').fadeIn(1000);
    }
    if(this.departmenterr){
      $('#errors').fadeIn(1000);
    }
    if(this.rollerr){
      $('#errors').fadeIn(1000);
    }
    if(this.gendererr){
      $('#errors').fadeIn(1000);
    }

    

  })
 
  
}

dismiss() {
  this.modalController.dismiss({
    'dismissed': true
  });
}






}
